// header
header {
    // slider
    .slider {
        .carousel-indicators {
            @include Xs {
                margin-bottom: 0;
            }
            li {
                width: 10px;
                height: 10px;
                border-radius: 50%;
                background-color: $par;
                @include Xs {
                    width: 7px;
                    height: 7px;
                }
                &.active {
                    background-color: $main-color;
                }
            }
            .carousel-control {
                width: 40px;
                height: 40px;
                top: 50%;
                transform: translateY(-50%);
                background-color: $par;
                @include border-radius(50%);
            }
            .carousel-control-prev {
                right: 30px;
                @media #{$medium} {
                    display: none;
                }
            }
            .carousel-control-next {
                left: 30px;
                @media #{$medium} {
                    display: none;
                }
            }
        }
    }
}

// slider
// .about-us
.about-us {
    padding: 70px 0px;
    text-align: center;
    @include Md {
        padding: 50px 0px;
    }
    @include Lg {
        padding: 70px 0px;
    }
    @include Sm {
        padding: 30px 0px;
    }
    h2 {
        @extend %head;
        position: relative;
        @include mob480 {
            font-size: 18px;
        }
        &::after {
            content: "";
            position: absolute;
            bottom: -20px;
            left: 50%;
            width: 100px;
            height: 2px;
            background-color: $main-color;
            transform: translateX(-50%);
        }
    }
    p {
        @extend %p;
        margin-top: 30px;
        margin-bottom: 30px;
        line-height: 2;
        @include Xs {
            font-size: 14px;
        }
    }
    a {
        color: $link;
        background-color: $blue;
        width: 140px;
        height: 40px;
        line-height: 40px;
        display: block;
        margin: auto;
        @include border-radius(100px);
        @include font-family('fr-Light-45');
        @extend %trans;
        &:hover {
            background-color: $main-color;
            color: $white;
        }
    }
}

// about us
// content
.content {
    padding: 70px 0px;
    text-align: center;
    @include Md {
        padding: 50px 0px;
    }
    @include Lg {
        padding: 70px 0px;
    }
    @include Sm {
        padding: 30px 0px;
    }
    h2 {
        @extend %head;
        font-size: 20px;
        @include font-family("fr-bold-65");
    }
    .images {
        @extend %flex;
        margin-top: 20px;
        flex-flow: wrap;
        @include Md {
            justify-content: start;
        }
        @include Lg {
            justify-content: space-between;
        }
        .card {
            border: none;
            width: 25%;
            padding: 0px 10px;
            @extend %trans;
            @include Md {
                width: 33.333%;
            }
            @include Lg {
                width: 25%;
            }
            @include Sm {
                width: 50%;
            }
            @include mob480 {
                width: 100%;
            }
            .card-head {
                cursor: pointer;
                overflow: hidden;
            }
            &:hover img {
                transform: scale(1.1);
            }
            &:hover a {
                color: $main-color;
            }
        }
    }
}

